<template>
  <div class="content">
    <section class="cenusContent">
      <div class="title">统计详细</div>
      <van-collapse v-model="activeNames">
        <van-collapse-item
          v-for="(item,index) in this.getRespone.cenusItem"
          :key="index"
          :name="index + 1"
        >
          <div slot="title">
            <section class="titleContent" v-for="(e,index) in item.personInfo" :key="index">
              <p>{{e.cardId}}</p>
              <p>{{e.region}}</p>
              <p>{{e.time}}</p>
              <p>{{e.num}}</p>
            </section>
            <div class="allPriceLine">
              <p>总数量：1</p>
              <p>总价格：2</p>
            </div>
          </div>
          <div class="contentItem" v-for="(el,index) in item.item" :key="index">
            <p>{{el.name}}</p>
            <p>￥{{el.price}}</p>
            <p>{{el.num}}</p>
            <p>￥{{el.money}}</p>
          </div>
        </van-collapse-item>
      </van-collapse>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeNames: ["1"],
    };
  },
  mounted(){
    var cenusItem = this.getRespone.cenusItem;
    var itemAll = [];
    var allNum = 0;
    var allPrice = 0;
    cenusItem.forEach(e =>{
      e.item.forEach(el => {
        allNum += el.num;
        allPrice += parseInt(el.money);
        itemAll.push(allNum,allPrice)
      })
    })
    console.log(JSON.stringify(itemAll))
  }
};
</script>

<style lang="scss" scoped>
.content {
  padding: 0.5rem;
  .cenusContent {
    border-radius: 0.5rem;
    box-shadow: 0rem 0.3rem 0.5rem $Tline;
    background-color: $fc;
    padding: 0.5rem;
    .title {
      @include sc(0.9rem, $fColor);
      padding: 0.5rem;
      font-weight: bold;
      @include wh(100%, auto);
      text-align: left;
    }
    .titleContent {
      @include fw(space-around);
      p {
        margin: 0.5rem 0.2rem;
        @include sc(0.8rem, $fColor);
      }
    }
    .allPriceLine{
      @include fr(flex-end);
      padding-top: 0.5rem;
      border-top: 0.05rem dotted $line;
      p:first-child{
        margin-right: 1rem;
      }
    }
    .contentItem{
      @include fw(space-around);
      p {
        margin: 0.5rem 0.2rem;
        @include sc(0.8rem, $fColor);
      }
    }
  }
}
/deep/ .van-cell {
  background-color: $F5;
}
</style>